import React, { useLayoutEffect, useState } from "react"

interface DropdownProps {
    children: React.ReactNode,
    overlay: React.ReactNode
}

const Dropdown = ({ children, overlay }: DropdownProps) => {

    const [open,setOpen] = useState(false)

    const closeDropdown  = () => {
        setOpen(false)
    }

    const openDropdown  = () => {
        setOpen(true)
    } 

    useLayoutEffect(()=>{
        document.documentElement.addEventListener("click",closeDropdown,true)
        return( ) => document.documentElement.removeEventListener("click",closeDropdown)
    },[])

    return (
        <div onMouseOver={openDropdown} onMouseLeave={closeDropdown} onClick={e => e.stopPropagation()} className="dropdown">
            <button onClick={openDropdown} aria-label="In your card" aria-expanded={open} aria-haspopup="true" >{children}</button>
            <div className=" relative flex left-8 sm:left-0 xl:justify-center justify-end">
                <div className="dropdown-overlay absolute hidden">{overlay}</div>
            </div>
        </div>
    )
}

export default Dropdown